/*换路由*/
.fade-enter {
  opacity:0;
}
.fade-leave{
  opacity:1;
}
.fade-enter-active{
  -webkit-transition:opacity .5s;
  transition:opacity .5s;
}
.fade-leave-active{
  opacity:0;
  -webkit-transition:opacity .5s;
  transition:opacity .5s;
}





























































































































































































































